
*,::before,::after{
  /*选择所有的标签*/
  margin:0;
  padding:0;
  /*清楚移动端默认的 点击高亮效果*/
  -webkit-tap-highlight-color:transparent;
  /*设置所有的都是以边框开始计算宽度 百分比*/
  -webkit-box-sizing:border-box;/*兼容*/
  box-sizing:border-box;
}
body{
  font-size:14px;
  font-family:"Microsoft YaHei",sans-serif;/*设备默认字体*/
  color:#333;
}
a{
  color:#333;
}
a:hover{
  text-decoration:none;/*不显示下划线*/
}
ul,ol{
  list-style:none;
}
input{
  border:none;
  outline:none;
  /*清除移动端默认的表单样式*/
  -webkit-appearance:none;
}
/*公共样式*/
.f_left{
  float:left;
}
.f_right{
  float:right;
}
.clearfix::before,.clearfix::after{
  content:"";
  height:0;
  line-height:0;
  display:block;
  visibility:hidden;
  clear:both;
}
#app {
  width: 100%;
  min-width: 300px;
  max-width: 500px;
  margin: 0 auto;
}
#app .footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 70px;
  text-align: center;
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  border-top: 1px solid #bbb;
}
#app .footer a {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-decoration: none;
}
#app .loading {
  width: 140px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  background-color: #eee;
  position: fixed;
  top: 50%;
  left: 35%;
  border: 1px solid #ccc;
  border-radius: 8px;
}
.index .space {
  width: 100%;
  height: 70px;
}
.index .swiper-button-prev {
  width: 30px;
  height: 30px;
  background: url(data:image/png;base64,) no-repeat 0 0;
}
.index .swiper-button-next {
  width: 30px;
  height: 30px;
  background: url(data:image/png;base64,) no-repeat -42px 0;
}
.index .swiper-slide img {
  width: 100%;
  height: 200px;
}
.fav-movie {
  margin: 4px 10px;
}
.fav-movie .img-title {
  float: left;
  width: 60px;
}
.fav-movie .img-title img {
  width: 100%;
}
.fav-movie .img-des {
  float: left;
  width: 70%;
  height: 100px;
  margin-left: 30px;
}
.head-content {
  dispaly: flex;
  display: -webkit-flex;
  width: 100%;
  height: 35px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-image: -webkit-linear-gradient(top, #3385ff, #388eff);
}
.head-content a:nth-of-type(1) {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 16px;
  color: #ddd;
  text-align: center;
  text-decoration: none;
}
.head-content input {
  -webkit-box-flex: 5;
      -ms-flex: 5;
          flex: 5;
  font-size: 15px;
  height: 25px;
  border: none;
  outline: none;
  border-radius: 10px;
  padding-left: 10px;
}
.head-content span {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 16px;
  color: #ddd;
  text-align: center;
  text-decoration: none;
}
.footer-item span:nth-child(1) {
  display: block;
  margin: 6px auto 1px;
  width: 35px;
  height: 35px;
  border: 1px solid #666;
  border-radius: 5px;
}
.footer-item span:nth-child(2) {
  font-size: 14px;
  color: #666;
  margin-bottom: 3px;
}
.footer-item .home-page {
  background: url(../../static/img/footer.5158a32.jpg) no-repeat -13px -13px;
  background-size: 280px 150px;
}
.footer-item .music {
  background: url(../../static/img/footer.5158a32.jpg) no-repeat -56px -13px;
  background-size: 280px 150px;
}
.footer-item .movies {
  background: url(../../static/img/footer.5158a32.jpg) no-repeat -99px -13px;
  background-size: 280px 150px;
}
.footer-item .set {
  background: url(../../static/img/footer.5158a32.jpg) no-repeat -144px -13px;
  background-size: 280px 150px;
}
.login {
  text-align: center;
  margin-top: 60px;
  padding-top: 10px;
}
.login input {
  dispaly: block;
  width: 70%;
  height: 45px;
  outline: none;
  font-size: 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 10px;
  margin-top: 25px;
  padding: 0 10px;
}
.login button {
  display: block;
  width: 70%;
  height: 45px;
  font-size: 16px;
  border-radius: 10px;
  margin: 25px auto 0;
  outline: none;
  border: 1px solid #ccc;
}
.myzone .user-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 17px 16px;
  border-bottom: 1px solid #e0e0e0;
  background-image: -webkit-linear-gradient(top, #388eff, #409ffd);
}
.myzone .user-info .user-img img {
  width: 50px;
  border-radius: 50%;
  border: 2px solid #fff;
}
.myzone .user-info .user-describe {
  margin: 5px 0 0 20px;
}
.music .fav-header {
  padding: 20px 10px;
  position: relative;
  overflow: hidden;
}
.music .fav-header .fav-header-bg {
  background: url(../../static/img/myImage.dc3c088.jpg) no-repeat;
  background-size: 100% 100%;
