  width: 145px;
  height: 145px;
  position: relative;
}
.music .fav-header .fav-header-wrap .header-left img {
  width: 100%;
}
.music .fav-header .fav-header-wrap .header-left span {
  position: absolute;
  top: 20px;
  left: 0;
  width: 50px;
  color: #fff;
