  border-radius: 0 10px 10px 0;
}
.music .fav-header .fav-header-wrap .header-right {
  padding: 20px 0 0 10px;
  color: #f5f5f5;
}
.music .fav-header .fav-header-wrap .header-right div {
  float: right;
  padding: 10px 10px 0 0;
}
.music .fav-intro {
  padding: 10px 10px;
}
.music .music-list {
  overflow: hidden;
}
.music .music-list .music-list-tit {
  height: 30px;
  line-height: 30px;
  padding-left: 10px;
  color: #666;
  background-color: #eeeff0;
}
.music .music-list .music-item {
  height: 55px;
}
.music .music-list .music-item h3 {
  width: 35px;
  height: 55px;
  line-height: 55px;
  text-align: center;
  float: left;
  color: #999;
  font-size: large;
}
.music .music-list .music-item .music-play {
  float: right;
  width: 22px;
  height: 22px;
  margin: 12px 15px 0 0;
  background-size: 166px 97px;
  background-image: url(../../static/img/music-icon.49a82fe.png);
